/**
 * Icons
 */
.icon {

    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;

        path {
            fill: $color-secondary;
        }
    }
}

body {
  width:100%;
  height:100%;
  background: url(data:image/png;base64,);
}

.fa {
  text-decoration: none;
}
